.container__screen2 {
	margin-top: 80px;
@media (max-width: 768px) {
	margin-top: 55px;
	}
}
.cards {
	font-family: var(--main-font);
	&__card-row {
		display: flex;
		// flex-wrap: wrap;
		flex: 1 1 45%;
		justify-content: space-around;
		align-items: center;
		text-indent: 0;
		margin: 10px -5px;
	}
	&__card {
		// display: flex;
		// flex: 1 1 45%;
		// margin: 5px 2px;
		max-width: 560px;
		// flex-direction: column;
		// justify-content: space-between;
	}

	&__card-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin: 5px;
		max-width: 560px;
		// height: 340px;
	}
	&__card-content span{
		margin: 10px 0;
}

	&__card-content img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__card-row {
		display: flex;
	}
	&__card-content span {
		font-weight: 600;
	}
	&__card-content p {
		margin: 0;
		padding: 0px;
	}
}

@media (max-width: 800px) {
	.cards {
		&__card {
			width: 100%;
			max-width: 800px;
		}
		&__card-content {
			max-width: 800px;
			height: auto;
		}

		&__card-row {
			flex: 1 1 100%;
			flex-direction: column;
		}
		&__card--left {
			flex-direction: column-reverse;
		}
	}
}

