.header {

  &__menu {
    position: relative;
  }
}
.menu {
  &_line {
    position: absolute;
    left: 0;
    bottom: 10px;
    width: 0;
    height: 2px;
    background-color: var(--color-white);
    transition: left 0.3s ease-in-out, width 0.3s ease-in-out;
    z-index: 6;
  }
  @media (max-width:767px) {
    &_line {
      display: none;
    }
}

}
.burger {
  display: none;
  &:before,
  &:after {
    content:"";
    background-color: var(--color-white);
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    transition: all 0.3s ease 0s;
  }
  &:before {
    top: 0px;
  }
  &:after {
    bottom: 0px;
  }
  & span {
    background-color: var(--color-white);
    position: absolute;
    top: 9px;
    left: 0;
    width: 100%;
    height: 2px;
    transition: all 0.3s ease 0s;
  }

  &.active span {
    transform: scale(0);
  }

  &.active:before {
    transform: rotate(45deg);
    top: 9px;
  }

  &.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  @media (max-width:767px) {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
      z-index: 6;
      cursor: pointer;
  }
}


.menu--list {
  display: flex;
  z-index: 6;

  &__item {
    font-size: 16px;
    &:not(:last-child){
      margin-right: 20px;
      font-weight: 400;
      font-family: var(--main-font);
    }
  }

  &__link {
    position: relative;
    text-transform: uppercase;
    color: var(--color-white);
  }
  // @media (max-width:860px) {
  //   &__item {
  //     font-size: 14px;
  //   }
  
  // }
  @media (max-width:767px) {
    flex-direction: column;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    justify-content: flex-start;
    align-items: flex-end;
    padding-top: 80px;
    background-color: #333;
    margin: 0;
    z-index: 2;
    transition: all 0.3s ease 0s;

    &.active {
      top: 0;
      transition: all 0.3s ease 0s;
    }

    &__item {
      margin-right: 20px;
      padding-top: 20px;
    }
  }
}