.teachers {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	text-indent: 0;
	text-align: center;
	font-family: var(--main-font);
	margin: 10px -5px;
	.teacher {
		&__card {
			display: flex;
			flex: 1 1 24%;
			margin: 10px 5px;
			max-width: 210px;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
		}

		&__card-photo {
			max-width: 200px;
			height: 200px;
		}
		&__card-photo img {
			border-radius: 100px;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&__card-title {
			font-size: 22px;
			
			line-height: 1.35;
			font-weight: 600;
			margin: 25px 0 15px 0;
		}

		&__card-desc {
			font-size: 14px;
			color: var(--color-gray);
		}

	}
}

@media (max-width: 680px) {
	.teachers {
		.teacher {
			&__card {
				flex: 1 1 50%;
			}
	}
	}
}

@media (max-width: 530px) {
	.teachers {
		.teacher {
			&__card {
				flex: 1 1 100% ;
				max-width: 100%;
			}
	}
	}
}
