.header {
	position: fixed;
	height: 50px;
	width: 100%;
	// background-color: #11111100;
	z-index: 5;
	// box-shadow: 0 0 10px rgba(0,0,0,0.5);

	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #11111100;
		z-index: 5;
	}

	&__container {
		padding: 2px 15px;
		display: flex;
		max-width: 1850px;
		align-items: center;
		justify-content: space-between;
		.menu__block {
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex: 1 1 auto;
      margin-left: 20px;
			@media (max-width: 767px) {
				margin: 0;
			}
	
		}
	}

	&__menu {
		display: flex;
	}

	&__logo {
		position: relative;
		flex: 0 0 40px;
		display: block;
		z-index: 6;
	}

	&__logo img {
		max-width: 100%;
	}
	@media (max-width: 767px) {
		height: 40px;
		padding-top: 8px;
		&:before {
			background-color: #333333ff;
		}
		&__logo {
			display: none;
		}
	}

	&__phone {
		font-size: 18px;
		z-index: 6;
		color: var(--color-white);
		&:before {
			content: "";
			position: absolute;
			height: 20px;
			width: 20px;
			top: 18px;
			margin-left: -25px;
			background: url(../img/phone.svg) no-repeat;
		}
		@media (max-width: 767px) {
			&:before {
				top: 11px;
			}
		}
	}
}

.menu--list {
	& a {
		&:before {
			content: "";
			position: absolute;
			background-color: var(--color-white);
			width: 100%;
			bottom: 0px;
			left: 0;
			height: 2px;
			opacity: 0;
			transition: opacity 0.5s;
		}
		&:hover:before {
			// content: "";
			// position: absolute;
			// background-color: var(--color-white);
			// width: 100%;
			bottom: -3px;
			// left: 0;
			// height: 2px;
			opacity: 1;
			transition: all 0.5s;
		}
	}

	a.active {
		color: var(--color-white);
		//    cursor: default;
		&:before {
			content: "";
			position: absolute;
			background-color: var(--color-white);
			width: 100%;
			bottom: -1px;
			left: 0;
			height: 1px;
			opacity: 1;
		}
	}
}
