.container {
	color: var(--color-black);
	text-align: center;
	&__title {
		position: relative;
		margin-top: 30px;
		&-title {
			margin: 0 0 100px 0;
			padding: 0;
			font-size: 36px;
			text-transform: uppercase;
			@media (max-width: 768px) {
				margin: 0 0 50px 0;
			}
	
		}
		&:after {
			content: "";
			position: absolute;
			bottom: -50px;
			margin-left: -50px;
			width: 100px;
			height: 2px;
			z-index: 2;
			background-color: var(--color-blue);
			@media (max-width: 768px) {
				bottom: -25px;
			}
		}
	}
	&__desc {
		margin: 50px 100px;
		font-family: var(--add-font);
		&-desc {
			font-size: 28px;
		}
		@media (max-width: 768px) {
			margin: 50px 25px;
			&-desc {
				font-size: 24px;
		}
		}

	}
	&__content {
		font-family: var(--add-font);
		font-size: 20px;
		text-indent: 40px;
		text-align: left;
	}
	.btn-link {
		font-family: var(--add-font);
		font-size: 16px;
		width: 200px;
		height: 60px;
		color: var(--color-white);
		background-color: var(--color-blue);
		text-transform: uppercase;
		border-radius: 30px;
		display: inline-block;
		margin: 60px 0 0 0;
		line-height: 60px;
		outline: none;
		text-decoration: none;
		@media (max-width: 768px) {
			margin: 0;
		}
	}
}
