.courses {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	text-indent: 0;
	font-family: var(--main-font);
	margin: 10px -5px;
	.course {
		&__card {
			display: flex;
			flex: 1 1 45%;
			margin: 10px 5px;
			max-width: 560px;
			flex-direction: column;
			justify-content: space-between;
		}

		&__card-photo {
			max-width: 560px;
			height: 340px;
		}
		&__card-photo img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&__card-title {
			font-size: 24px;
			line-height: 1.35;
			font-weight: 600;
			margin: 25px 0;
		}

		&__card-desc {
			font-size: 14px;
		}

		&__card-btn {
			margin-top: 25px;
		}
		&__card-btnlink {
			font-family: var(--add-font);
			font-size: 13px;
			width: 100px;
			height: 35px;
			margin-top: 25px;
			color: var(--color-white);
			background-color: var(--color-red);
			text-transform: uppercase;
			border-radius: 5px;
			display: inline-block;
			margin: 0px 0 0 0;
			line-height: 35px;
			outline: none;
			text-align: center;
			text-indent: 0;
			text-decoration: none;
		}
	}
}

@media (max-width: 800px) {
	.courses {
		.course {
			&__card {
				flex: 1 1 45%;
			}
	
			&__card-title {
				font-size: 20px;
				line-height: 1.35;
				font-weight: 600;
				margin: 25px 0;
			}
	
			&__card-desc {
				font-size: 14px;
			}
		}
	}
}
	
@media (max-width: 680px) {
		.courses {
			.course {
				&__card {
					flex: 1 1 100%;
				}
		
				&__card-title {
					font-size: 20px;
					line-height: 1.35;
					font-weight: 600;
					margin: 25px 0;
				}
		
				&__card-desc {
					font-size: 14px;
				}
			}
		}
}
	