html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  color: var(--color-black);
  background-color: var(--color-white);
}

@media (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: fixed;
  height: 50px;
  width: 100%;
  z-index: 5;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #11111100;
  z-index: 5;
}

.header__container {
  padding: 2px 15px;
  display: flex;
  max-width: 1850px;
  align-items: center;
  justify-content: space-between;
}

.header__container .menu__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 1 auto;
  margin-left: 20px;
}

@media (max-width: 767px) {
  .header__container .menu__block {
    margin: 0;
  }
}

.header__menu {
  display: flex;
}

.header__logo {
  position: relative;
  flex: 0 0 40px;
  display: block;
  z-index: 6;
}

.header__logo img {
  max-width: 100%;
}

@media (max-width: 767px) {
  .header {
    height: 40px;
    padding-top: 8px;
  }
  .header:before {
    background-color: #333333ff;
  }
  .header__logo {
    display: none;
  }
}

.header__phone {
  font-size: 18px;
  z-index: 6;
  color: var(--color-white);
}

.header__phone:before {
  content: "";
  position: absolute;
  height: 20px;
  width: 20px;
  top: 18px;
  margin-left: -25px;
  background: url(../img/phone.svg) no-repeat;
}

@media (max-width: 767px) {
  .header__phone:before {
    top: 11px;
  }
}

.menu--list a:before {
  content: "";
  position: absolute;
  background-color: var(--color-white);
  width: 100%;
  bottom: 0px;
  left: 0;
  height: 2px;
  opacity: 0;
  transition: opacity 0.5s;
}

.menu--list a:hover:before {
  bottom: -3px;
  opacity: 1;
  transition: all 0.5s;
}

.menu--list a.active {
  color: var(--color-white);
}

.menu--list a.active:before {
  content: "";
  position: absolute;
  background-color: var(--color-white);
  width: 100%;
  bottom: -1px;
  left: 0;
  height: 1px;
  opacity: 1;
}

.header__menu {
  position: relative;
}

.menu_line {
  position: absolute;
  left: 0;
  bottom: 10px;
  width: 0;
  height: 2px;
  background-color: var(--color-white);
  transition: left 0.3s ease-in-out, width 0.3s ease-in-out;
  z-index: 6;
}

@media (max-width: 767px) {
  .menu_line {
    display: none;
  }
}

.burger {
  display: none;
}

.burger:before, .burger:after {
  content: "";
  background-color: var(--color-white);
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  transition: all 0.3s ease 0s;
}

.burger:before {
  top: 0px;
}

.burger:after {
  bottom: 0px;
}

.burger span {
  background-color: var(--color-white);
  position: absolute;
  top: 9px;
  left: 0;
  width: 100%;
  height: 2px;
  transition: all 0.3s ease 0s;
}

.burger.active span {
  transform: scale(0);
}

.burger.active:before {
  transform: rotate(45deg);
  top: 9px;
}

.burger.active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}

@media (max-width: 767px) {
  .burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 6;
    cursor: pointer;
  }
}

.menu--list {
  display: flex;
  z-index: 6;
}

.menu--list__item {
  font-size: 16px;
}

.menu--list__item:not(:last-child) {
  margin-right: 20px;
  font-weight: 400;
  font-family: var(--main-font);
}

.menu--list__link {
  position: relative;
  text-transform: uppercase;
  color: var(--color-white);
}

@media (max-width: 767px) {
  .menu--list {
    flex-direction: column;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    justify-content: flex-start;
    align-items: flex-end;
    padding-top: 80px;
    background-color: #333;
    margin: 0;
    z-index: 2;
    transition: all 0.3s ease 0s;
  }
  .menu--list.active {
    top: 0;
    transition: all 0.3s ease 0s;
  }
  .menu--list__item {
    margin-right: 20px;
    padding-top: 20px;
  }
}

.intro {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  color: var(--color-white);
  text-align: center;
  /* begin Scroll Down Button */
  /* end Scroll Down Button */
}

.intro .wrapper__down {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 2;
}

.intro button.scroll_down {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 50px;
  width: 50px;
  bottom: 60px;
  background-color: transparent;
  border: 0px solid white;
  border-radius: 50px;
  cursor: pointer;
  outline: none;
}

.intro button.scroll_down:before {
  position: absolute;
  top: -3px;
  left: 4px;
  content: "";
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 100%;
  background: url(../img/down.svg) no-repeat;
  -webkit-animation: scroll_down_btn-animation 2s infinite;
  animation: scroll_down_btn-animation 2s infinite;
  box-sizing: border-box;
}

@keyframes scroll_down_btn-animation {
  0% {
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes scroll_down_btn-animation {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.intro__slider {
  width: 100%;
  height: 100vh;
  z-index: 5;
}

.intro .active {
  opacity: 1;
  transition: all 1.5s linear;
}

.intro .active img {
  width: 100%;
  height: 100%;
  transition: all 1.5s linear;
}

.slider {
  position: relative;
}

.slider__slide {
  opacity: 0;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transition: all 1.5s linear;
}

.slider__slide:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
}

.slider__slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main__contant {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: space-around;
  justify-content: center;
  align-items: center;
  z-index: 2;
  color: var(--color-white);
}

.main__contant-title {
  margin: 24px 10px 38px 10px;
}

.main__contant-title span {
  font-size: 68px;
}

@media (max-width: 720px) {
  .main__contant-title span {
    font-size: 48px;
  }
}

@media (max-width: 520px) {
  .main__contant-title span {
    font-size: 40px;
  }
}

.main__contant-desc {
  padding: 0 10px;
}

.main__contant-desc span {
  font-size: 32px;
}

@media (max-width: 520px) {
  .main__contant-desc span {
    font-size: 26px;
  }
}

.container__screen2 {
  margin-top: 80px;
}

@media (max-width: 768px) {
  .container__screen2 {
    margin-top: 55px;
  }
}

.cards {
  font-family: var(--main-font);
}

.cards__card-row {
  display: flex;
  flex: 1 1 45%;
  justify-content: space-around;
  align-items: center;
  text-indent: 0;
  margin: 10px -5px;
}

.cards__card {
  max-width: 560px;
}

.cards__card-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 5px;
  max-width: 560px;
}

.cards__card-content span {
  margin: 10px 0;
}

.cards__card-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cards__card-row {
  display: flex;
}

.cards__card-content span {
  font-weight: 600;
}

.cards__card-content p {
  margin: 0;
  padding: 0px;
}

@media (max-width: 800px) {
  .cards__card {
    width: 100%;
    max-width: 800px;
  }
  .cards__card-content {
    max-width: 800px;
    height: auto;
  }
  .cards__card-row {
    flex: 1 1 100%;
    flex-direction: column;
  }
  .cards__card--left {
    flex-direction: column-reverse;
  }
}

.container {
  color: var(--color-black);
  text-align: center;
}

.container__title {
  position: relative;
  margin-top: 30px;
}

.container__title-title {
  margin: 0 0 100px 0;
  padding: 0;
  font-size: 36px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .container__title-title {
    margin: 0 0 50px 0;
  }
}

.container__title:after {
  content: "";
  position: absolute;
  bottom: -50px;
  margin-left: -50px;
  width: 100px;
  height: 2px;
  z-index: 2;
  background-color: var(--color-blue);
}

@media (max-width: 768px) {
  .container__title:after {
    bottom: -25px;
  }
}

.container__desc {
  margin: 50px 100px;
  font-family: var(--add-font);
}

.container__desc-desc {
  font-size: 28px;
}

@media (max-width: 768px) {
  .container__desc {
    margin: 50px 25px;
  }
  .container__desc-desc {
    font-size: 24px;
  }
