.teachers .teacher__card {
  display: flex;
  flex: 1 1 24%;
  margin: 10px 5px;
  max-width: 210px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.teachers .teacher__card-photo {
  max-width: 200px;
  height: 200px;
}

.teachers .teacher__card-photo img {
  border-radius: 100px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.teachers .teacher__card-title {
  font-size: 22px;
  line-height: 1.35;
  font-weight: 600;
  margin: 25px 0 15px 0;
}

.teachers .teacher__card-desc {
  font-size: 14px;
  color: var(--color-gray);
}

@media (max-width: 680px) {
  .teachers .teacher__card {
    flex: 1 1 50%;
  }
}

@media (max-width: 530px) {
  .teachers .teacher__card {
    flex: 1 1 100%;
    max-width: 100%;
  }
}

.sales {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-indent: 0;
  text-align: center;
  font-family: var(--main-font);
  margin: 10px -5px;
}

.sales .sale__card {
  display: flex;
  flex: 1 1 33%;
  margin: 10px 5px;
  max-width: 315px;
  min-width: 315px;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 9px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.sales .sale__card-title {
  font-size: 20px;
  font-weight: 600;
  margin: 45px 0 20px 0;
}

.sales .sale__card-desc {
  font-size: 14px;
  margin: 20px 0 30px 0;
  color: var(--color-gray);
}

.sales .sale__card-price {
  font-size: 38px;
  font-weight: 600;
}

.sales .sale__card-btn {
  margin-top: 35px;
}

.sales .sale__card-btnlink {
  font-family: var(--add-font);
  font-size: 13px;
  width: 100px;
  height: 35px;
  color: var(--color-white);
  background-color: var(--color-red);
  text-transform: uppercase;
  border-radius: 5px;
  display: inline-block;
  margin: 0 0 45px 0;
  line-height: 35px;
  outline: none;
  text-align: center;
  text-indent: 0;
  text-decoration: none;
}

.bglight {
  background-color: var(--color-bglight);
}

.bgdark {
  background-color: var(--color-gray);
}

.section {
  padding-top: 50px;
  text-align: center;
}

.section__title {
  position: relative;
  text-transform: uppercase;
}

.section__title:before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  bottom: -26px;
  margin-left: -3px;
  background-color: var(--color-black);
  transform: rotate(45deg);
}

.section__title:after {
  content: "";
  position: absolute;
  padding: 0;
  width: 30%;
  margin-left: -15%;
  height: 2px;
  bottom: -20px;
  background-color: var(--color-black);
}

@media (max-width: 1200px) {
  .section {
    padding-top: 35px;
  }
}

.title__header {
  font-size: 32px;
}

.title__desc {
  font-size: 18px;
}
