.section {
//	height: 100vh;
	padding-top: 50px;
	text-align: center;
	&__title {
		position: relative;
		text-transform: uppercase;
		&:before {
			content: "";
			position: absolute;
			width: 14px;
			height: 14px;
			bottom: -26px;
			margin-left: -3px;
			background-color: var(--color-black);
			transform: rotate(45deg);
		}
		&:after {
			content: "";
			position: absolute;
			padding: 0;
			width: 30%;
			margin-left: -15%;
			height: 2px;
			bottom: -20px;
			background-color: var(--color-black);
		}
	}
	@media (max-width:1200px) {
		padding-top: 35px;
