.sales {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	text-indent: 0;
	text-align: center;
	font-family: var(--main-font);
	margin: 10px -5px;
	.sale {
		&__card {
			display: flex;
			flex: 1 1 33%;
			margin: 10px 5px;
			max-width: 315px;
			min-width: 315px;
			flex-direction: column;
			justify-content: space-between;
			border-radius: 9px;
			box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
		}

		&__card-title {
			font-size: 20px;
			font-weight: 600;
			margin: 45px 0 20px 0;
		}

		&__card-desc {
			font-size: 14px;
			margin: 20px 0 30px 0;
			color: var(--color-gray);
		}

		&__card-price {
			font-size: 38px;
			font-weight: 600;
		}

		&__card-btn {
			margin-top: 35px;
		}
		&__card-btnlink {
			font-family: var(--add-font);
			font-size: 13px;
			width: 100px;
			height: 35px;
			color: var(--color-white);
			background-color: var(--color-red);
			text-transform: uppercase;
			border-radius: 5px;
			display: inline-block;
			margin: 0 0 45px 0;
			line-height: 35px;
			outline: none;
			text-align: center;
			text-indent: 0;
			text-decoration: none;
		}
	}
}
