.intro {
	// position: absolute;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	color: var(--color-white);
	text-align: center;

	.wrapper__down {
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		position: relative;
		z-index: 2;
	}

	/* begin Scroll Down Button */
	button.scroll_down {
		position: absolute;
		margin-left: auto;
		margin-right: auto;
		left: 0;
		right: 0;
		height: 50px;
		width: 50px;
		bottom: 60px;
		background-color: transparent;
		border: 0px solid white;
		border-radius: 50px;
		cursor: pointer;
		outline: none;
	}

	button.scroll_down:before {
		position: absolute;
		top: -3px;
		left: 4px;
		content: "";
		width: 40px;
		height: 40px;
		background-color: #fff;
		border-radius: 100%;
		background: url(../img/down.svg) no-repeat;
		-webkit-animation: scroll_down_btn-animation 2s infinite;
		animation: scroll_down_btn-animation 2s infinite;
		box-sizing: border-box;
	}

	@keyframes scroll_down_btn-animation {
		0% {
			transform: translate(0, 0);
			opacity: 0;
		}
		40% {
			opacity: 1;
		}
		80% {
			transform: translate(0, 20px);
			opacity: 0;
		}
		100% {
			opacity: 0;
		}
	}

	@-webkit-keyframes scroll_down_btn-animation {
		0% {
			-webkit-transform: translate(0, 0);
			transform: translate(0, 0);
			opacity: 0;
		}
		40% {
			opacity: 1;
		}
		80% {
			-webkit-transform: translate(0, 20px);
			transform: translate(0, 20px);
			opacity: 0;
		}
		100% {
			opacity: 0;
		}
	}

	/* end Scroll Down Button */

	&__slider {
		width: 100%;
		height: 100vh;
		z-index: 5;
	}
	.active {
		opacity: 1;
		transition: all 1.5s linear;
		img {
			width: 100%;
			height: 100%;
			transition: all 1.5s linear;
		}
	}
}

.slider {
	position: relative;

	&__slide {
		opacity: 0;
		position: absolute;
		display: flex;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		transition: all 1.5s linear;
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			background-color: rgba(0, 0, 0, 0.5);
		}

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
.main {
	&__contant {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-content: space-around;
		justify-content: center;
		align-items: center;
		z-index: 2;
		color: var(--color-white);
	}

	&__contant-title {
		margin: 24px 10px 38px 10px;
		span {
			font-size: 68px;
			@media (max-width: 720px) {
				font-size: 48px;
			}
			@media (max-width: 520px) {
				font-size: 40px;
			}
		}
	}

	&__contant-desc {
		padding: 0 10px;
		span {
			font-size: 32px;
			@media (max-width: 520px) {
				font-size: 26px;
			}
		}
	}
}

